import "whatwg-fetch";

const PROD_LAMBDA_URL =
  "https://xdc9dmik2f.execute-api.us-west-2.amazonaws.com/Prod/submit_order/";

// const DEV_LAMBDA_URL = "TODO";

// yeah, not the best thing to do here
export const generateOrderId = () => {
  return Math.floor(100000000 + Math.random() * 900000000);
};

export const submitOrderToBackend = (sourceParams, chargeParams) => {
  return fetch(PROD_LAMBDA_URL, {
    method: "POST",
    headers: {
      "Content-Type": "application/json"
    },
    body: JSON.stringify({ sourceParams, chargeParams })
  });
};

// Frontend JS doesn't have access to CORS errors :(
// So in Sentry they just look like "failed to fetch"
